 
.testimonial-section {
    padding: 40px 20px;
    text-align: center;
}

.testimonial-section h2 {
    font-size: 24px;
    color: #333;
}

.testimonial-section h2 span {
    color: #ff5722;
}

.testimonial-section p {
    color: #666;
    margin-bottom: 20px;
}

.testimonial-cards {
    display: flex;
    overflow-x: auto;
    padding-bottom: 10px;
    scroll-snap-type: x mandatory;
}

.testimonial-cards::-webkit-scrollbar {
    display: none;  
}

.testimonial-card {
    scroll-snap-align: start;
    flex-shrink: 0;
}
