/* src/styles/Footer.css */
.footer {
    background-color: #f8eae6;
    padding: 40px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    text-align: left;
}

.brand {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.social-links {
    display: flex;
    gap: 15px;
}

.social-icon {
    font-size: 24px;
    color: #555; /* Adjust color as per your preference */
    cursor: pointer;
}

.footer-links {
    flex: 3;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-left: 20px;
}

.column h4 {
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 16px;
}

.column ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.column ul li {
    margin-bottom: 8px;
    font-size: 14px;
    color: #333;
}

@media (max-width: 768px) {
    .footer {
        flex-direction: column;
        align-items: flex-start;
    }

    .footer-links {
        flex-direction: column;
        width: 100%;
        margin-left: 0;
    }
}
