 
.testimonial-card {
    background-color: #fff;
    padding: 20px;
    margin: 0 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.card-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 10px;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.user-info h4 {
    margin: 0;
    color: #ff5722;
}

.user-info p {
    font-size: 14px;
    color: #666;
}

.testimonial-text {
    font-size: 14px;
    color: #444;
    margin: 10px 0;
}

.rating .star {
    color: #ccc;
    font-size: 18px;
}

.rating .filled {
    color: #ffdd57;
}
