.service-card {
    background-color: #fff;
    border-radius: 8px;
    padding: 1rem;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    width: 150px;
    text-align: center;
  }
  
  .service-icon {
    font-size: 2rem;
  }
  
  .read-more-button {
    background-color: #ff5722;
    color: white;
    border: none;
    padding: 0.5rem;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 1rem;
  }
  