.our-agency {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    background-color: #ffffff;
  }
  
  .agency-content {
    flex: 1;
    padding-right: 1rem;
  }
  
  .agency-content h2 {
    font-size: 2rem;
    color: #333;
  }
  
  .agency-content span {
    color: #ff5722;
  }
  
  .read-more {
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #ff5722;
    color: white;
    border: none;
    cursor: pointer;
  }
  