.hero {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
  }
  
  .hero-content h1 {
    font-size: 2.5rem;
    font-weight: bold;
  }
  
  .hero-content span {
    color: #ff5722;
  }
  
  .get-started-button, .explore-button {
    background-color: #ff5722;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-radius: 5px;
    margin-right: 1rem;
  }
  
  .hero-image img {
    width: 100%;
    max-width: 300px;
  }
  