.simple-solutions {
    display: flex;
    padding: 2rem;
    background-color: #f8e9e0;
  }
  
  .solution-content {
    flex: 1;
    padding-right: 1rem;
  }
  
  .solution-content h2 {
    font-size: 2rem;
    color: #333;
  }
  
  .solution-content span {
    color: #ff5722;
  }
  
  .solution-content ul {
    list-style-type: none;
    padding: 0;
  }
  
  .solution-content ul li {
    margin: 0.5rem 0;
  }
  
  .buttons {
    margin-top: 1rem;
  }
  
  .get-started, .read-more {
    padding: 0.5rem 1rem;
    margin-right: 0.5rem;
    border: none;
    cursor: pointer;
  }
  
  .get-started {
    background-color: #ff5722;
    color: white;
  }
  
  .read-more {
    background-color: transparent;
    color: #ff5722;
    border: 1px solid #ff5722;
  }
  