.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: white;
  }
  
  .navbar-brand {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ff5722;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    gap: 1.5rem;
  }
  .navbar-links span{
      color: #ff5722;
  }
  
  .navbar-links a {
    text-decoration: none;
    color: #333;
  }
  
  .sign-up-button {
    background-color: #ff5722;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-radius: 5px;
  }
  