.cta-section {
    background: #e16e0e;
    color: #fff;
    display: flex;
   align-items: center;
   justify-content: space-between;
    padding: 15px 10px;
    margin-top: 40px;
    border-radius: 5px;
}

.cta-section h2 {
    font-size: 1.8em;
    margin: 0 0 15px;
     
    
}

.contact-button {
   
    background-color: #e16e0e;
    color: white;
    padding: 10px 20px;
    margin-right: 10px;
    border: 2px solid white;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1em;
}
